import { Component, Input, OnInit } from '@angular/core';
import { AutoApplyConstraints } from './opcua-protocol-device-type.interface';
import { IManagedObject, InventoryService } from '@c8y/client';
import { gettext } from '@c8y/ngx-components';
import { set } from 'lodash-es';
@Component({
  selector: 'opcua-auto-apply',
  templateUrl: './opcua-auto-apply-settings.component.html'
})
export class OpcuaAutoApplySettingsComponent implements OnInit {
  opcuaServers: IManagedObject[] = [];
  selected: IManagedObject[] = [];
  constraints: AutoApplyConstraints = {
    browsePathMatchesRegex: '',
    matchesNodeIds: [],
    serverObjectHasFragment: '',
    matchesServerIds: []
  };
  placeholderSelectServerIds: string = gettext('Select server IDs from list');
  showServerIds: boolean;
  showBrowsePath: boolean;
  showServerFragment: boolean;
  showRootNodes: boolean;
  private _model: IManagedObject;

  constructor(private inventoryService: InventoryService) {}

  async ngOnInit() {
    const { data } = await this.inventoryService.list({
      pageSize: 1000,
      withTotalPages: true,
      type: 'c8y_OpcuaServer'
    });

    this.opcuaServers = data;
    this.selected = [];

    const { matchesServerIds } = this.constraints;

    data.forEach(server => {
      if (
        matchesServerIds &&
        matchesServerIds.length > 0 &&
        matchesServerIds.find(itemId => itemId === server.id)
      ) {
        this.selected.push(server);
      }
    });
  }

  @Input() set model(model) {
    if (model && model.applyConstraints) {
      this.constraints = model.applyConstraints as AutoApplyConstraints;
    }
    this._model = model;
  }

  get model() {
    return this._model;
  }

  serverIdsSelected(items: IManagedObject[]) {
    if (this.constraints) {
      this.constraints.matchesServerIds = items.map((item: IManagedObject) => item.id);
    }
    this.selected = items;
  }

  onChangeNodeId(event) {
    if (event.target.checked) {
      this.showRootNodes = true;
      this.add();
    } else {
      this.showRootNodes = false;
      this.constraints.matchesNodeIds = [];
    }
  }

  onChangeShowServerIds(event) {
    if (!event.target.checked) {
      this.constraints.matchesServerIds = [];
      this.showServerIds = false;
      this.selected = [];
    } else {
      this.showServerIds = true;
    }
  }

  onChangeShowBrowsePath(event) {
    if (!event.target.checked) {
      this.constraints.browsePathMatchesRegex = '';
      this.showBrowsePath = false;
    } else {
      this.showBrowsePath = true;
    }
  }

  onChangeShowServerFragment(event) {
    if (!event.target.checked) {
      this.constraints.serverObjectHasFragment = '';
      this.showServerFragment = false;
    } else {
      this.showServerFragment = true;
    }
  }

  add() {
    this.constraints.matchesNodeIds.push('');
  }

  remove(index) {
    this.constraints.matchesNodeIds.splice(index, 1);
  }

  trackByFn(index: any, item: any) {
    return index;
  }
}
