import { Component, Input, Output, EventEmitter } from '@angular/core';
import { AddressSpaceNode, AddressSpaceService, NodeNavigationData } from './address-space.service';
import { omit } from 'lodash-es';

@Component({
  selector: 'opcua-address-space-detail',
  templateUrl: './opcua-address-space-detail.component.html'
})
export class OpcuaAddressSpaceDetailComponent {
  @Input() set node(n) {
    this._node = n;
    if (n) {
      this.setNodeData(n);
    } else {
      // remove details from current view
      this.showDetails = false;
    }
  }
  nodeDataAttr: Map<string, string>;
  nodeDataRef: object[];
  selected: boolean = false;
  showDetails: boolean = false;

  @Output() toggleAttrDetail: EventEmitter<AddressSpaceNode> = new EventEmitter<AddressSpaceNode>();

  private _node: AddressSpaceNode;

  constructor(private addressSpaceService: AddressSpaceService) {}
  setNodeData(nodeData) {
    this.showDetails = true;
    const { attributes, references } = nodeData;
    this.nodeDataRef = references;
    const omitList = [
      'attributes',
      'references',
      'children',
      'currentlyLoadingChildren',
      'expanded',
      'browsePath',
      'relativePath',
      'parentNode'
    ];
    this.nodeDataAttr = Object.assign({}, attributes, omit(nodeData, omitList));
  }

  toggleDetail(node) {
    this.showDetails = !this.showDetails;
    this.toggleAttrDetail.emit(node);
  }

  navigateTo(ancestors: string[]) {
    const nodeNavData: NodeNavigationData = {
      node: this._node,
      selectedAncestorIds: ancestors
    };

    this.toggleDetail(this._node);
    this.addressSpaceService.triggerNodeToOpen(nodeNavData);
  }
}
