import { Injectable } from '@angular/core';
import { FetchClient, IFetchOptions, InventoryService, InventoryBinaryService } from '@c8y/client';
import { OpcuaServer } from './opcua-server.interface';
import { Router } from '@angular/router';
import { AlertService } from '@c8y/ngx-components';

@Injectable()
export class OpcuaService {
  private binaryService: InventoryBinaryService;
  private microserviceUrl: string;
  private deviceTypeProtocolUrl: string;
  private header: any;

  constructor(
    private client: FetchClient,
    private inventoryService: InventoryService,
    private router: Router,
    private alertService: AlertService
    ) {
    this.microserviceUrl = '/service/opcua-mgmt-service/server';
    this.deviceTypeProtocolUrl = '/service/opcua-mgmt-service/deviceTypes';
    this.header = { 'Content-Type': 'application/json' };
    this.binaryService = inventoryService.binary;
  }

  getServers(id: string) {
    if (id && id.length > 0) {
      const options: IFetchOptions = {
        method: 'GET',
        headers: this.header
      };
      return this.client.fetch(`${this.microserviceUrl}/${id}`, options);
    }
  }

  createServer(data: OpcuaServer) {
    if (this.doesGatewayIdExist(data)) {
      this.cleanUpPayload(data);
      const options: IFetchOptions = {
        method: 'POST',
        headers: this.header,
        body: JSON.stringify(data)
      };
      return this.client.fetch(`${this.microserviceUrl}`, options);
    }
  }

  async updateServer(server: OpcuaServer) {
    if (this.doesGatewayIdExist(server) && this.doesIdExist(server)) {
      this.cleanUpPayload(server);
      const options: IFetchOptions = {
        method: 'POST',
        headers: this.header,
        body: JSON.stringify(server)
      };
      const res = await this.client.fetch(`${this.microserviceUrl}`, options);
      let data;
      try {
        data = await res.json();
      } catch (e) {
        // nothing
      }

      if (res.status !== 200) {
        this.alertService.addServerFailure({ data, res });
      } else {
        return data;
      }
    }
  }

  removeServer(data: OpcuaServer) {
    if (this.doesGatewayIdExist(data) && this.doesIdExist(data)) {
      const options: IFetchOptions = {
        method: 'DELETE'
      };
      return this.client.fetch(`${this.microserviceUrl}/${data.gatewayId}/${data.id}`, options);
    }
  }

  getKeystore(binaryId: string) {
    if (binaryId && binaryId.length > 0) {
      return this.inventoryService.detail(binaryId);
    }
    return null;
  }

  uploadKeystore(file: File) {
    if (file && file.size > 0) {
      return this.binaryService.create(file);
    }
    return Promise.reject('Invalid file');
  }

  async updateKeystore(id: string, file: File) {
    if (id && id.length > 0 && file && file.size > 0) {
      const { res } = await this.removeKeystore(id);
      if (res && res.status === 204) {
        return this.uploadKeystore(file);
      }
    }
    return Promise.reject('Invalid file');
  }

  removeKeystore(id: string) {
    if (id && id.length > 0) {
      return this.binaryService.delete(id);
    }
  }

  getMoId() {
    const currentUrl: string = this.router.routerState.snapshot.url;
    const isDevice: boolean = new RegExp(/device\/\d+/).test(currentUrl);
    if (isDevice) {
      return currentUrl.match(/\d+/)[0];
    }
    return '';
  }

  getId() {
    const currentUrl: string = this.router.routerState.snapshot.url;
    const isDeviceprotocol: boolean = new RegExp(/deviceprotocols/).test(currentUrl);
    if (isDeviceprotocol && RegExp(/\d+$/).test(currentUrl)) {
      return currentUrl.match(/\d+$/)[0];
    }
  }

  async getDeviceProtocol(id: string) {
    const options: IFetchOptions = {
      method: 'GET',
      headers: this.header,
      };
    return this.client.fetch(`${this.deviceTypeProtocolUrl}/${id}`, options);

  }

  async updateDeviceProtocol(data) {
    const options: IFetchOptions = {
      method: 'PUT',
      headers: this.header,
      body: JSON.stringify(data)
    };
    return this.client.fetch(`${this.deviceTypeProtocolUrl}/${data.id}`, options);

  }

  async createDeviceProtocol(data) {
    const options: IFetchOptions = {
      method: 'POST',
      headers: this.header,
      body: JSON.stringify(data)
    };
    return this.client.fetch(`${this.deviceTypeProtocolUrl}`, options);
  }

  private doesGatewayIdExist(data: OpcuaServer) {
    return data && data.gatewayId && data.gatewayId.length > 0;
  }

  private doesIdExist(data: OpcuaServer) {
    return data && data.id && data.id.length > 0 && data.id !== 'new';
  }

  private cleanUpPayload(data: OpcuaServer) {
    if (data) {
      if (data.id && data.id === 'new') { delete data.id; }
      if (data.quickInfo) { delete data.quickInfo; }
    }
  }
}
