import { BehaviorSubject, Observable, merge, Subscription } from 'rxjs';
import { NestedTreeControl } from '@angular/cdk/tree';
import { CollectionViewer, SelectionChange } from '@angular/cdk/collections';
import { AddressSpaceService, AddressSpaceNode } from './address-space.service';
import { map } from 'rxjs/operators';

export class DynamicDataSource {
  dataChange = new BehaviorSubject<AddressSpaceNode[]>([]);

  get data(): AddressSpaceNode[] {
    return this.dataChange.value;
  }
  set data(value: AddressSpaceNode[]) {
    this.treeControl.dataNodes = value;
    this.dataChange.next(value);
  }

  constructor(
    public treeControl: NestedTreeControl<AddressSpaceNode>,
    private addressSpaceService: AddressSpaceService,
    private serverId: string
  ) {
    this.treeControl.isExpanded = (node: AddressSpaceNode) => node.expanded;
  }

  connect(collectionViewer: CollectionViewer): Observable<AddressSpaceNode[]> {
    this.treeControl.expansionModel.onChange.subscribe(
      (change: SelectionChange<AddressSpaceNode>) => {
        if (change.added || change.removed) {
          this.handleTreeControl(change);
        }
      }
    );
    return merge(collectionViewer.viewChange, this.dataChange).pipe(map(() => this.data));
  }

  /** Handle expand/collapse behaviors */
  handleTreeControl(change: SelectionChange<AddressSpaceNode>) {
    if (change.added) {
      change.added.forEach(node => this.toggleNode(node, true));
    }
    if (change.removed) {
      change.removed
        .slice()
        .reverse()
        .forEach(node => this.toggleNode(node, false));
    }
  }

  /**
   * Toggle the node, remove from display list
   */
  async toggleNode(addressSpaceNode: AddressSpaceNode, expand: boolean) {
    if (!addressSpaceNode.children || addressSpaceNode.children.length === 0) {
      addressSpaceNode.currentlyLoadingChildren = true;

      const res = await this.addressSpaceService.getChildrenOf(addressSpaceNode, this.serverId);
      const children = (await res.json()) as AddressSpaceNode[];

      addressSpaceNode.children = children || [];
      addressSpaceNode.children = addressSpaceNode.children.map((node: AddressSpaceNode) => {
        node.parentNode = addressSpaceNode;
        return node;
      });
      addressSpaceNode.currentlyLoadingChildren = false;

      this.treeControl.expand(addressSpaceNode);
    }

    addressSpaceNode.expanded = expand && addressSpaceNode.children.length > 0;
    this.refreshNestedTree(this.data);

    return Promise.resolve(addressSpaceNode);
  }
  catch() {
    // do nothing
  }

  private refreshNestedTree(treeData: AddressSpaceNode[]) {
    // necessary to rerender tree, otherwise new nodes will not
    // appear, but they are added to the list.
    this.data = [];
    this.dataChange.next(treeData);
    this.triggerResize(); // to resize the modal window when creating a new device protocol
  }

  private triggerResize() {
    setTimeout(() => {
      try {
        window.dispatchEvent(new Event('resize'));
      } catch (error) {
        // do nothing
      }
    }, 200);
  }
}
