import { Component, Input, Output, EventEmitter } from '@angular/core';
import { OpcuaServer } from './opcua-server.interface';

@Component({
  selector: 'opcua-server-list',
  templateUrl: './opcua-server-list.component.html'
})
export class OpcuaServerListComponent {
  @Input() serverList: OpcuaServer[] = [];
  @Output() present = new EventEmitter<OpcuaServer>();

  presentConfig(server: OpcuaServer) {
    server.active = true;
    this.present.emit(server);
  }
}
