import { Injectable } from '@angular/core';
import { FetchClient, IFetchOptions } from '@c8y/client';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable()
export class AddressSpaceService {
  private nodeNavigationData$: BehaviorSubject<NodeNavigationData>;
  private client: FetchClient;
  private microserviceUrl: string;
  private header: any;

  constructor(fetchClient: FetchClient) {
    this.client = fetchClient;
    this.microserviceUrl = '/service/opcua-mgmt-service/address-space';
    this.header = { 'Content-Type': 'application/json' };
    this.nodeNavigationData$ = new BehaviorSubject<NodeNavigationData>({
      node: undefined,
      selectedAncestorIds: []
    });
  }

  resetTreeToRootNode() {
    this.triggerNodeToOpen({ node: undefined, selectedAncestorIds: [] });
  }

  triggerNodeToOpen(nodeNavigationData: NodeNavigationData) {
    this.nodeNavigationData$.next(nodeNavigationData);
  }

  getNodeNavData$(): Observable<NodeNavigationData> {
    return this.nodeNavigationData$.asObservable();
  }

  getNode(serverId: string, nodeId?: string) {
    if (serverId && serverId.length > 0) {
      if (nodeId && nodeId.length > 0) {
        return this.getNodeById(serverId, nodeId);
      }
      return this.getRootNode(serverId);
    }
  }

  getRootNode(serverId: string) {
    if (serverId && serverId.length > 0) {
      const options: IFetchOptions = {
        method: 'GET',
        headers: this.header
      };
      return this.client.fetch(`${this.microserviceUrl}/${serverId}`, options);
    }
  }

  getNodeById(serverId: string, nodeId: string) {
    if (serverId && nodeId && serverId.length > 0 && nodeId.length > 0) {
      const options: IFetchOptions = {
        method: 'GET',
        headers: this.header
      };
      const param = encodeURIComponent(nodeId);
      return this.client.fetch(`${this.microserviceUrl}/${serverId}?nodeId=${param}`, options);
    }
  }

  getChildrenOf(node: AddressSpaceNode, serverId: string) {
    if (serverId && node.nodeId && serverId.length > 0 && node.nodeId.length > 0) {
      const options: IFetchOptions = {
        method: 'GET',
        headers: this.header
      };
      const param = encodeURIComponent(node.nodeId);
      return this.client.fetch(
        `${this.microserviceUrl}/${serverId}/children?nodeId=${param}`,
        options
      );
    }
  }

  childrenAvailable(nodeReferences: AdressSpaceNodeReference[]): boolean {
    if (!nodeReferences || nodeReferences.length === 0) {
      return false;
    }
    return nodeReferences.some(ref => !ref.inverse && ref.hierarchical);
  }

  async getSearchedNodes(searchKey: string, serverId: string) {
    const url = `service/opcua-mgmt-service/search/${serverId}/`;
    const options: IFetchOptions = {
      headers: this.header,
      params: {
        searchString: '*' + searchKey + '*'
      }
    };
    const res = await this.client.fetch(url, options);
    return res.json();
  }

  getIcon(nodeClassName: string) {
    const iconList = {
      Object: 'cube',
      Variable: 'th-list',
      Method: 'random',
      View: 'window-maximize',
      ObjectType: 'c8y-group',
      VariableType: 'c8y-group',
      ReferenceType: 'c8y-group',
      DataType: 'c8y-group'
    };
    return iconList[nodeClassName] || 'circle';
  }
}

export interface AddressSpaceNode {
  nodeId: string;
  currentlyLoadingChildren?: boolean;
  nodeClass?: number;
  nodeClassName?: string;
  browseName?: string;
  displayName?: string;
  description?: any;
  references?: AdressSpaceNodeReference[];
  ancestorNodeIds?: [string[]];
  children: AddressSpaceNode[];
  expanded: boolean;
  absolutePaths: [string[]];
  relativePath?: string[];
  parentNode?: AddressSpaceNode;
}

export interface AdressSpaceNodeReference {
  referenceId: string;
  targetId: string;
  inverse: boolean;
  hierarchical: boolean;
}

export interface SearchedNode {
  absolutePath: string[];
  ancestorNodeIds: [string[]];
  displayName: string;
  nodeClassName: string;
  nodeId: string;
}

export interface NodeNavigationData {
  node: AddressSpaceNode;
  selectedAncestorIds: string[];
}
