import { Injectable } from '@angular/core';
import { CanActivate } from '@angular/router';
import { AppStateService } from '@c8y/ngx-components';

@Injectable()
export class OpcuaMicroserviceGuard implements CanActivate {
  microserviceName: string = 'opcua-mgmt-service';
  checkApp: Promise<boolean>;

  constructor(
    private app: AppStateService
  ) {}

  canActivate() {
    if (!this.checkApp) {
      this.checkApp = this.app.isApplicationAvailable(this.microserviceName);
    }
    return this.checkApp;
  }
}
